  cursor: pointer;
  font-size: 16px;
}

.signIn-link:hover {
  text-decoration: underline;
}

/* the container where the forms are in  */

.signInContainer, .signUpContainer {
  width: 200px;
  height: 300px;
  padding: 20px;
  border: 0.5px solid rgb(224, 221, 221);
  margin-top: 60px;
  display: table;
  background-color: #464646;
}

.have-acct {
  font-size: 14px;
  font-weight: bold;
  font-family: "Montserrat", sans-serif;
  text-align: center;
  margin-top: 0;
}

.anchor {
  color: orange;
  cursor: pointer;
  font-size: 16px;
}

.anchor:hover {
  text-decoration: underline;
}

.need-acct {
  font-size: 15;
  font-size: 14px;
  font-weight: bold;
  font-family: "Montserrat", sans-serif;
  text-align: center;
}

.create-acct, .log-acct {
  font-size: 40px;
  font-family: "Montserrat", sans-serif;
  text-align: left;
  margin-bottom: 30px;
  font-weight: 100;
  color: rgb(235, 180, 78);
  letter-spacing: 0;
}

/* all the inputs  */

.userNameInput, .emaiInput, .passwordInput, .confirmSignInput {
  height: 50px;
  margin-top: 8px;
  width: 380px;
  border-radius: 5px;
  padding: 0px 8px;
  font-size: 16;
  outline: none;
  border: none;
  font-size: 14px;
  border-bottom: 2px solid rgba(0, 0, 0, 0.3);
  align-content: center;
}

/* labels for each input */

.labelsForm {
  height: 0;
  font-weight: 100;
  color: white
}

.labelUsername {
  margin-left: -14px;
}

/* all the buttons  */

.signInbutton, .confirmSignInButton, .signUpButton {
  background: rgb(255, 153, 102);
  background: -webkit-gradient( linear, left top, right top, color-stop(9%, rgba(255, 153, 102, 1)), color-stop(48%, rgba(255, 153, 102, 1)), to(rgba(255, 94, 98, 0.8914916308320203)));
  background: linear-gradient( 90deg, rgba(255, 153, 102, 1) 9%, rgba(255, 153, 102, 1) 48%, rgba(255, 94, 98, 0.8914916308320203) 100%);
  color: white;
  width: 380px;
  height: 45px;
  margin-top: 16px;
  margin-bottom: 15px;
  font-weight: 600px;
  font-size: 14px;
  cursor: pointer;
  border: none;
  outline: none;
  border-radius: 3px;
  font-family: "Muli", sans-serif;
  box-shadow: 0px 1px 4px rgba(0, 0, 0, 0.3);
}

.signInbutton:hover, .confirmSignInButton:hover, .signUpButton:hover {
  box-shadow: 1px 3px 2px 1px rgb(31, 30, 30);
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}

.errorStyle {
  color: red;
  font-size: 12px;
  text-align: left;
  font-family: "Muli", sans-serif;
}
/* ------------Same footer to all pages------------*/
.appFooter {
